                  </el-popover>
                </div>
              </div>
              <div class="upload_upload">
                <el-upload class="upload-demo" :on-preview="handlePreview" :on-change="handleChange1" drag
                  :action="upload" :limit="1" :before-upload="beforeUpload">
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                  <div class="el-upload__tip" slot="tip">只能上传单个Excel文件</div>
                </el-upload>
              </div>
            </div>

            <div class="upload_item left">
              <div class="upload_title">
                <div class="inco">
                  <img src="../assets/image/260d6ea9b8032348c239f46fafea35f.png" alt="">
                </div>
                <div class="text">市或区县防控清单</div>
                <el-popover trigger="hover">
                    <span>
                      命名要求：<br>
                      在文件名称后面加后缀，标明文件属于什么级别 如：<br>
                      1.区县文件：<br>
                      文件名称_区县公司<br>
                      2.地市文件：<br>
                      文件名称_市公司<br>
                      3.政企文件：<br>
                      文件名称_政企领域<br>
                      4.省级文件：<br>
                      文件名称_省级单位<br>
                    </span>
                    <i color="red" style="margin-left: 12px;cursor: pointer;" slot="reference" class="el-icon-warning-outline"></i>

                  </el-popover>
              </div>
              <div class="upload_upload">
                <el-upload class="upload-demo" :on-preview="handlePreview" :on-change="handleChange" drag multiple
                  :action="upload" :before-upload="beforeUpload">
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                  <div class="el-upload__tip" slot="tip">只能上传Excel文件</div>
                </el-upload>
              </div>
            </div>


          </div>

          <div class="res_box">
            <div class="res_title"> <i class="el-icon-document-copy"></i> 对比结果</div>
            <div class="res_list">
              <div class="res_item" v-for="(item, index) in list" :key="index">
                <div class="res_item_title">
                  <div class="inco">
                    <img src="../assets/image/a1c1b4fcf384bf15ab02c385a0ae552.png" alt="">
                  </div>
                  <div class="text">{{ item.resultfileName }}</div>
                </div>
                <div class="res_content">
                  <div class="content_item" :class="{ 'full-screen': fullScreenIndex === index }">
                    <span class="exit-button" @click="exitFullScreen(index)" v-if="fullScreenIndex === index">X</span>
                    <div v-html="item.resultfileHtml"></div>
                  </div>
