      <div class="right_box">
        <div class="right_title">
          <div class="inco">
            <img src="../assets/image/a164144b3fe2c100ac78c4c753a863b.png" alt="">
          </div>
          <div class="text">历史记录</div>
        </div>
        <div class="list_box">
          <div class="rightlist_item" v-for="(ele, y) in historys" :key="y" @click="open(ele)">
            {{ ele.contrastName }}
          </div>
        </div>
      </div>
    </div>


    <el-dialog title="对比结果" :visible.sync="outerVisible">
      <div>
        <div class="father_box">
          <span>父文件</span>：<a :href="lists.fatherfileUrl">{{ lists.fatherfileName }}</a>
        </div>
        <div class="log_list">
          <div class="log_list_item" v-for="(element, i) in lists.resultList" :key="i">

            <div class="log_text">
              <div class="log_inco">
                <img src="../assets/image/a1c1b4fcf384bf15ab02c385a0ae552.png" alt="">
              </div>
              <div class="log_title">{{ element.resultfileName }}</div>
            </div>
            <div class="log_content" :class="{ 'full-screen': fullScreenIndex1 === i }">
              <span class="exit-button" @click="exitFullScreen(i, 1)" v-if="fullScreenIndex1 === i">X</span>
              <div v-html="element.resultfileHtml"></div>
            </div>

            <div class="log_button">
              <div class="d_box">
                对比风险点：{{ element.riskpointTotal }} 处
              </div>
              <div>
                <el-button type="primary" size="small" @click="toggleFullScreen(i, 1)"
                  icon="el-icon-zoom-in">全屏</el-button>
                <el-button type="primary" size="small" icon="el-icon-folder-opened"
                  @click="download(element, 1)">源文件</el-button>
                <el-button type="primary" size="small" icon="el-icon-upload el-icon--right"
                  @click="download(element, 2)">导出</el-button>
              </div>

            </div>

          </div>
        </div>
