  </div>
</template>

<script>
export default {
  data() {
    return {
      fullscreenLoading: false,//loding
      upload: '/api/addFile',
      fileList: [],//多文件地址
      fileurl: [],//单文件地址
      list: [],//生成结果
      fullScreenIndex: null,//全屏弹窗下标
      historys: [],//历史记录列表
      outerVisible: false,//历史记录弹窗
      fullScreenIndex1: null,//历史记录弹窗的全屏弹窗下标
      lists: [],//历史记录弹窗生成结果
    };
  },
  mounted() {
    // console.log(this.upload)
    this.gethistory()
  },
  methods: {
    //单文件状态监听
    handleChange1(file, fileList) {
      // console.log(fileList)
      this.fileurl = fileList;
    },
    //多文件状态监听
    handleChange(file, fileList) {
      this.fileList = fileList;
    },
    // 文件预览
    handlePreview(file) {
      let link = document.createElement('a');
      link.href = file.response.data;
      link.download = file.name;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    //下载文件
    download(e, type) {
      // console.log(e)
      let url = null
      let name = null
      if (type && type == 1) {
        url = e.childfileUrl
        name = e.childfileName
      } else {
        url = e.resultfileUrl
        name = e.resultfileName
      }
      let link = document.createElement('a');
      link.href = url;
      link.download = name;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    //提交文件，生成结果
    Submit() {
      const fileurl = this.fileurl.map(item => item.response.data);
      const fileList = this.fileList.map(item => item.response.data);
      if (!fileurl[0]) {
        this.$message.error('请上传一个省或市防控清单文件!');
        return
      }
      if (fileList.length == 0) {
        this.$message.error('请至少上传一个市或区县防控清单文件!');
        return
      }
      const loading = this.$loading({
        lock: true,
        text: '结果生成中，请稍等...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      let data = {
        childUrlList: fileList.length > 1 ? fileList.join(',') : fileList[0],
        fatherUrl: fileurl[0]
      };
      this.$http.post("/api/contrast", data).then(res => {
        if (res.code == 200) {
          this.list = res.data
          loading.close();
        } else {
          this.$message.error(res.msg || '生成失败!');
          loading.close();
        }
      })
        .catch((err) => {
          console.log(err)
          this.$message.error('生成失败!');
          loading.close();
        });


    },
    //获取历史记录
    gethistory() {
      let data = {};
      this.$http.get("/api/history", data).then(res => {
        if (res.code == 200) {
          this.historys = res.data
        }
      });
    },
    //历史记录弹窗
    open(e) {
      let data = {
        contrastld: e.id,
      };
      this.$http.get("/api/info", data).then(res => {
        if (res.code == 200) {
          this.lists = res.data
          this.outerVisible = true
        }
      });

    },
    // 全屏
    toggleFullScreen(index, type) {
      if (type && type == 1) {
        if (this.fullScreenIndex1 === index) {
          this.fullScreenIndex1 = null;
        } else {
          this.fullScreenIndex1 = index;
        }
      } else {
        if (this.fullScreenIndex === index) {
          this.fullScreenIndex = null;
        } else {
          this.fullScreenIndex = index;
        }
      }

    },
    //退出全屏
    exitFullScreen(index, type) {
      this.fullScreenIndex = null;
      this.fullScreenIndex1 = null;
    },
    //限制格式
    beforeUpload(file) {
      const isExcelFile = file.type === 'application/vnd.ms-excel' || file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
      const isExcelExtension = file.name.endsWith('.xls') || file.name.endsWith('.xlsx');
      if (!isExcelFile || !isExcelExtension) {
        this.$message.error('只能上传 Excel 文件');
      }
      return isExcelFile && isExcelExtension;
    },

    //  ==
  },
};
</script>
